.body {
  display: flex;
  flex-direction: column;
  max-width: 1920px;
  width: 100%;
  margin: auto;
  margin-top: 200px;

  & > div {
    margin-bottom: 160px;
  }
}

@media screen and (max-width: 375px) {
  .body {
    margin-top: 72px;

    & > div {
      margin-bottom: 70px;
    }
  }
}
